<template>
	<div id="kefu-index">
		<public-header :title="title"></public-header>
		<div class="content">
			<template v-if="type == 'kefu_status'">
				<div class="padding-10-tb padding-15-lr font-14 c-gray">店员客服状态</div>
				<van-radio-group v-model="deliveryer.kefu_status">
					<van-cell-group>
						<van-cell title="在线" clickable @click="deliveryer.kefu_status = '1'">
							<van-radio name="1" />
						</van-cell>
						<van-cell title="忙碌" clickable @click="deliveryer.kefu_status = '2'">
							<van-radio name="2" />
						</van-cell>
						<van-cell title="离线" clickable @click="deliveryer.kefu_status = '3'">
							<van-radio name="3" />
						</van-cell>
					</van-cell-group>
				</van-radio-group>
			</template>
			<template v-else-if="type == 'busy_reply'">
				<div class="padding-10-tb padding-15-lr font-14 c-gray">忙碌状态自动回复</div>
				<van-cell-group>
					<van-field
						v-model="deliveryer.extra.kefu_busy_reply"
						type="textarea"
						placeholder="当对话为忙碌状态时, 将自动回复此内容给顾客"
						rows="4"
					/>
				</van-cell-group>
			</template>
			<div class="padding-15">
				<van-button size="normal" class="bg-primary" :disabled="!islegal" block @click="onSubmit">保存修改</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import {getData, setData} from '@/controller/funcCommon'
	export default {
		data() {
			return {
				type: '',
				title: '',
				deliveryer: {
					kefu_status: '1',
					extra: {
						kefu_busy_reply: ''
					}
				},
				islegal: false,
				showPreLoading: true
			}
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'delivery/kefu/setting/index',
					data: {},
					success: (res) => {
						that.deliveryer = that.util.extend(that.deliveryer, res.deliveryer);
						that.islegal = true;
					}
				})
			},

			onSubmit() {
				var that = this;
				if(!that.islegal) {
					return;
				}
				that.islegal = false;
				var data = {
					type: that.type
				};
				if(that.type == 'kefu_status') {
					data['kefu_status'] = that.deliveryer.kefu_status;
				} else if(that.type == 'busy_reply') {
					data['busy_reply'] = that.deliveryer.extra.kefu_busy_reply;
				}
				setData({
					vue: that,
					url: 'delivery/kefu/setting/update',
					data: data,
					success: (res) => {
						that.util.$toast(res, that.util.getUrl({path: '/pages/kefu/setting'}), 2000, 'replace')
					}
				})
			}
		},
		mounted() {
			var that = this;
			var query = that.util.parseQuery(that.$route.query);
			that.type = query.type;
			if(that.type == 'kefu_status') {
				that.title = '客服状态';
			} else if(that.type == 'busy_reply') {
				that.title = '忙碌状态自动回复';
			}
			that.util.setWXTitle(that.title);
			that.onLoad();
		},
		components: {
			PublicHeader,
		},
	}
</script>

<style>
	@import "../../../static/css/test.css";
</style>


